<template>
	<ul
		class="task-list">
		<TaskListItem v-for="task in tasks"
			:key="task.id"
			class="task-list--item"
			:task="task"
			@try-again="$emit('try-again', task)"
			@load="$emit('load-task', task)"
			@delete="onTaskDelete(task)"
			@cancel="onTaskCancel(task)" />
	</ul>
</template>

<script>
import TaskListItem from './TaskListItem.vue'

import axios from '@nextcloud/axios'
import { generateOcsUrl } from '@nextcloud/router'

import { STATUS } from '../constants.js'

export default {
	name: 'TaskList',

	components: {
		TaskListItem,
	},

	props: {
		taskType: {
			type: [String, null],
			default: null,
		},
		loading: {
			type: Boolean,
			default: false,
		},
	},

	emits: [
		'load-task',
		'try-again',
	],

	data() {
		return {
			tasks: [],
		}
	},

	computed: {
	},

	watch: {
		taskType() {
			this.getTasks()
		},
	},

	mounted() {
		this.getTasks()
	},

	methods: {
		getTasks() {
			this.$emit('update:loading', true)
			const req = {
				params: {
					taskType: this.taskType,
				},
			}
			const url = generateOcsUrl('/apps/assistant/api/v1/tasks')
			axios.get(url, req).then(response => {
				this.tasks = response.data?.ocs?.data?.tasks
			}).catch(error => {
				console.error(error)
			}).then(() => {
				this.$emit('update:loading', false)
			})
		},
		onTaskDelete(task) {
			const url = generateOcsUrl('/apps/assistant/api/v1/task/{id}', { id: task.id })
			axios.delete(url).then(response => {
				const index = this.tasks.findIndex(t => { return t.id === task.id })
				if (index !== -1) {
					this.tasks.splice(index, 1)
				}
			}).catch(error => {
				console.error(error)
			})
		},
		onTaskCancel(task) {
			const url = generateOcsUrl('/apps/assistant/api/v1/task/cancel/{id}', { id: task.id })
			axios.put(url).then(response => {
				task.status = STATUS.failed
				task.output = t('assistant', 'Canceled by user')
			}).catch(error => {
				console.error(error)
			})
		},
	},
}
</script>

<style lang="scss">
.task-list {
	&--item {
		width: 99% !important;
	}
}
</style>
